import React, { useEffect, useState } from 'react'
import { Table, Tag, Button, Space, Menu, Dropdown } from 'antd';
import { EditOutlined, DeleteOutlined, SearchOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';
import { ModalComponent } from '../ui/ModalComponent';
import { prestadoresStartLoading, startSetingActivePrestador } from '../../actions/prestadores';
import { useDispatch, useSelector } from 'react-redux';
import { ValoresEstudios } from './ValoresEstudios';
import { Sucursales } from './Sucursales';
import { AddComment } from './AddComment';
import { uiOpenModal } from '../../actions/ui';

  
export const GridPrestadores = () => {
  
  const dispatch = useDispatch();
  

  const { prestadoresList } = useSelector( state => state.prestadores );

  const showModal = (key)=>{
    
    switch (key.key) {
      case "1":
          dispatch(uiOpenModal({component: <ValoresEstudios />, title: 'Valores de Estudios',record: key.key}));
          break;
      case "2":
          dispatch(uiOpenModal({component: <Sucursales />, title: 'Sucursales',record: key.key}));
          break;
      case "3":
          dispatch(uiOpenModal({component: <AddComment />, title: 'Comentarios',record: key.key}));
          break;
      
          default:
            break;
    
        }
  } 

  const menu = (
    <Menu>
      <Menu.Item key="1" onClick={showModal}>Ver Estudios</Menu.Item>
      <Menu.Item key="2" onClick={showModal}>Ver Sucursales</Menu.Item>
      <Menu.Item key="3" onClick={showModal}>Cargar Notas</Menu.Item>
    </Menu>
  );
  

  const data = prestadoresList.map(prestador=>(


    {
      key: prestador.id,
      nombre: prestador.nombre,
      zona: prestador.zona,
      direccion: prestador.domicilio,
      especialidad: prestador.especialidad,
    }

  ))
  
  
  const [info, setInfo] = useState({
    filteredInfo: null,
    sortedInfo: null,
  });
  
  const { filteredInfo, sortedInfo } = info
  

  const columns = [
    {
      title: 'Nombre',
      dataIndex: 'nombre',
      key: 'nombre',
      render: text => <Link to="/prestadores/consulta">{text}</Link>
    },
    {
      title: 'Direccin',
      dataIndex: 'direccion',
      key: 'direccion',
    },
    {
      title: 'Zona',
      dataIndex: 'zona',
      key: 'zona',
      filters: [
        { text: 'Zona SUR', value: 'AMBA, Zona SUR' },
        { text: 'Zona NORTE', value: 'AMBA, Zona NORTE' },
        { text: 'Zona OESTE', value: 'AMBA, Zona OESTE' },
        { text: 'CABA', value: 'CABA' },
      ],
      filteredValue: filteredInfo?.zona || null,
      onFilter: (value, record) => record.zona.includes(value),
      sorter: (a, b) => a.zona.length - b.zona.length,
      sortOrder: sortedInfo?.columnKey === 'zona' && sortedInfo.order,
      ellipsis: true,
    },
    {
      title: 'Especialidad',
      key: 'especialidad',
      dataIndex: 'especialidad',
      render: especialidad => (
        <span>
          {especialidad.map(tag => {
            let color = tag.length > 10 ? 'geekblue' : 'green';
            if (tag === 'loser') {
              color = 'volcano';
            }
            return (
              <Tag color={color} key={tag}>
                {tag}
              </Tag>
            );
          })}
        </span>
      ),
      filters: [
        { text: 'Paediatra', value: 'Pediatra' },
        { text: 'Traumatologa', value: 'Traumatologa' },
        { text: 'Cardiologa', value: 'Cardiologa' },
        { text: 'Ciruga', value: 'Ciruga' },
        { text: 'Odontologa', value: 'Odontologa' }
      ],
      filteredValue: filteredInfo?.especialidad || null,
      onFilter: (value, record) => record.especialidad.includes(value),
    //  sorter: (a, b) => a.especialidad.length - b.especialidad.length,
      //sortOrder: sortedInfo?.columnKey === 'especialidad' && sortedInfo.order,
      ellipsis: false,

    },
    {
      title: 'Action',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
           <Link to="/prestadores/consulta"><EditOutlined /></Link>
           <Link to="/prestadores/consulta"><DeleteOutlined /></Link>
          <Link to="/prestadores/consulta"><SearchOutlined /></Link>
        </Space>
      ),
    },
  ];

const handleChange = (pagination, filters, sorter) => {
  
  setInfo({
    filteredInfo: filters,
    sortedInfo: sorter,
  });
};

const clearFilters = () => {
  setInfo({ filteredInfo: null });
};

const clearAll = () => {
  setInfo({
    filteredInfo: null,
    sortedInfo: null,
  });
};

const setAgeSort = () => {
  setInfo({
    sortedInfo: {
      order: 'descend',
      columnKey: 'nombre',
    },
  });
};

useEffect(() => {
  dispatch(prestadoresStartLoading());
}, [dispatch])


const eventRow = (record) => {
  return {
    onClick: event => {dispatch(startSetingActivePrestador(record))}, // click row
    onDoubleClick: event => {}, // double click 
    onContextMenu: event => {
      
      dispatch(startSetingActivePrestador(record))

   },
    onMouseEnter: event => {}, // mouse enter row
    onMouseLeave: event => {}, // mouse leave row
  };
}

  return (
    <>
    <ModalComponent/>
  
    <Space style={{ marginBottom: 16 }}>
          <Button onClick={ setAgeSort }>Ordenar Nombre</Button>
          <Button onClick={ clearFilters }>Limpiar Filtros</Button>
          <Button onClick={ clearAll }>Limpiar todo</Button>
    </Space>
    

    <Dropdown overlay={menu} trigger={['contextMenu']}>
          <div>          
          <Table  columns={columns}
                  onRow={eventRow}          
                  pagination={{ position: 'bottomCenter', pageSize: 12, pageSizeOptions: [5,10,25,50] }}
                  dataSource={data}
                  onChange={ handleChange }
        />
        </div>

    </Dropdown>
        </>
    )
}
