import React, { useCallback, useEffect, useState } from 'react'
import { Form, Input,  Select, Row, Col, Button } from 'antd';
import { fechtConjwt } from '../../helpers/fecht';



const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  
export const StepForm2 = () => {


  const [provincias, setProvincias] = useState([])
  const [localidades, setLocalidades] = useState([])
  

    const onFinish = (values) => {
        console.log('Received values of form: ', values);
      };

      const [form] = Form.useForm();

      const { Option } = Select;


      const handleProvinceChange = async (value)=> {
        console.log(`selected ${value}`);
        
        try {
            const res = await fechtConjwt(`maps/getLocalidades/${value}`)
            const body = await res.json();

            
            if(body.ok){
                const localidadArray = body.localidades;
                setLocalidades(localidadArray)
                console.log(localidadArray)
            }

        } catch (error) {
            console.log(error)
        }

        
         
        
      }
      

function onChange(){}

function onBlur() {
  console.log('blur');
}

function onFocus() {
  console.log('focus');
}

function onSearch(val) {
  console.log('search:', val);
}


useEffect(() => {

  fechtConjwt('maps/getProvincias')
  .then(res=>{return res.json()})
  .then(data=>{
  
    setProvincias(data.provincias)
  
  })
    
}, [])

function toUpper(str) {
  return str
      .toLowerCase()
      .split(' ')
      .map(function(word) {
         // console.log("First capital letter: "+word[0]);
         // console.log("remain letters: "+ word.substr(1));
          return word[0].toUpperCase() + word.substr(1);
      })
      .join(' ');
    }

    return (
        <>

<Row>
    <Col span={12}>
            <Form
    {...formItemLayout}
    form={form}
    name="register"
    size={"small"}
    labelAlign={"left"}
    onFinish={onFinish}
    initialValues={{
      residence: ['zhejiang', 'hangzhou', 'xihu'],
      prefix: '54',
    }}
    scrollToFirstError
  >

<Form.Item
      name="nombreSucursal"
      label="Nombre Sucursal"
      tooltip=""
      rules={[{ required: true, message: 'Ingrese un nombre para la sucursal', whitespace: true }]}
    >
      <Input placeholder="Sucursal 3 - Pacheco" />
</Form.Item>

<Form.Item
      name="domicilioSucursal"
      label="Domicilio"
      tooltip="Ingresar solamente calle y Altura"
      rules={[{ required: true, message: 'Ingrese el domiciolio de la sucursal', whitespace: true }]}
    >
      <Input placeholder="Sarmiento N 425" />
</Form.Item>



<Form.Item
name="provincia"
label="Provincia"
rules={[{ required: true, message: 'Please input your nickname!', whitespace: true }]}
>

<Select
    showSearch
    placeholder="Buenos Aires"
    optionFilterProp="children"
    onChange={handleProvinceChange}
    onFocus={onFocus}
    onBlur={onBlur}
    onSearch={onSearch}
 >
{provincias.map(provincia=>(

<Option key={provincia.nombre} value={provincia.id}>{provincia.nombre}</Option>

    ))}

  
    
  </Select>


</Form.Item>

<Form.Item
name="localidad"
label="Localidad"
rules={[{ required: true, message: 'Please input your nickname!', whitespace: true }]}
>

<Select
    showSearch
    placeholder="Berazategui"
    optionFilterProp="children"
    onChange={onChange}
    onFocus={onFocus}
    onBlur={onBlur}
    onSearch={onSearch}
 >
     
     {localidades.map(localidad => (
          <Option key={localidad._id}>{toUpper(localidad.nombre.toLowerCase())}</Option>
        ))}  




    </Select>


</Form.Item>


<Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit">
          Cargar
        </Button>
      </Form.Item>

</Form>

</Col>
</Row>



        </>
    )
}
