import React, { useState } from 'react'
import { Form,
         Input,
         Select,
         Row,
         Col,
         AutoComplete,
         } from 'antd';


const { Option } = Select;

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};
/*const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};*/



export const StepForm1 = () => {

    const onFinish = (values) => {
        console.log('Received values of form: ', values);
      };
      
      const prefixSelector = (
        <Form.Item name="prefix" noStyle>
          <Select style={{ width: 70 }}>
            <Option value="011">011</Option>
            <Option value="0221">0221</Option>
            
          </Select>
        </Form.Item>
      );
      
      const [autoCompleteResult, setAutoCompleteResult] = useState([]);
      
      const onWebsiteChange = (value) => {
        if (!value) {
          setAutoCompleteResult([]);
        } else {
          setAutoCompleteResult(['.com','.com.ar', '.org', '.net'].map(domain => `${value}${domain}`));
        }
      };
      
      const websiteOptions = autoCompleteResult.map(website => ({
        label: website,
        value: website,
      }));

      const especialidades = ["Cardiologa", "Urologa", "Dermatologa", "Traumatologa"]

      const handleChangeEspecialidad = (value)=>{console.log(value)}
      
      const [form] = Form.useForm();

    return (
        <>
           <Row>
        <Col span={12}>
        
{/*Formulario de Datos Bsicos*/}
  
  <Form
    {...formItemLayout}
    form={form}
    name="register"
    size={"small"}
    labelAlign={"left"}
    onFinish={onFinish}
    initialValues={{
      prefix: '011',
    }}
    scrollToFirstError
  >



{/*Nombre del prestador*/}

<Form.Item
      name="nombre"
      label="Nombre Prestador"
      tooltip="Preferentemente ingresar la razon social!"
      rules={[{ required: true, message: 'El nombre del prestador es obligatorio', whitespace: true }]}
    >
      <Input />
    </Form.Item>

{/*CUIT del prestador*/}

<Form.Item
      name="nombre"
      label="CUIT"
      tooltip=""
      rules={[{ required: true, message: 'El CUIT del prestador es obligatorio', whitespace: true }]}
    >
      <Input />
    </Form.Item>

{/*Email del prestador*/}

<Form.Item
      name="email"
      label="E-mail"
      rules={[
        {
          type: 'email',
          message: 'El email ingresado no es vlido',
        },
        {
          required: true,
          message: 'El email del prestador es obligatorio',
        },
      ]}
    >
      <Input />
    </Form.Item>

{/*Domicilio del prestador*/}

<Form.Item
      name="domicilio"
      label="Domicilio"
      tooltip=""
      rules={[{ required: true, message: 'El domicilio del prestadores obligatorio', whitespace: true }]}
    >
      <Input />
    </Form.Item>

{/*Telefono del prestador*/}

<Form.Item
      name="telefono"
      label="Telefono"
      rules={[{ required: true, message: 'El telefono es obligatorio' }]}
    >
      <Input addonBefore={prefixSelector} style={{ width: '100%' }} />
    </Form.Item>

{/*Whatsapp del prestador*/}

<Form.Item
      name="whatsapp"
      label="Whatsapp"
      rules={[{ required: false, message: 'Please input your phone number!' }]}
    >
      <Input addonBefore={prefixSelector} style={{ width: '100%' }} />
    </Form.Item>

{/*Website del prestador*/}

<Form.Item
      name="website"
      label="Website"
      rules={[{ required: false, message: 'Please input website!' }]}
    >
      <AutoComplete options={websiteOptions} onChange={onWebsiteChange} placeholder="website">
        <Input />
      </AutoComplete>
    </Form.Item>

{/*Especialidad del prestador*/}

<Form.Item
      name="especialidad"
      label="Especialidad"
      rules={[{ required: true, message: 'La especialidad es obligatoria.' }]}
    >
      <Select
    mode="multiple"
    style={{ width: '100%' }}
    placeholder="Selecciones al menos una especialdiad"
    //defaultValue={['Clnica']}
    onChange={handleChangeEspecialidad}
    optionLabelProp="label"
  >

      {especialidades.map(especialidad=>(

<Option key={especialidad} value={especialidad} label={especialidad}>
<div className="demo-option-label-item">
   {especialidad}
 </div>
</Option>

      ))}




    </Select>
    </Form.Item>

  </Form>

        </Col>
    </Row>
      
        </>
    )
}
